import React, { useState } from 'react';
import { ScrollView, View } from 'react-native';
import './App.css';
import Text from './app/components/Text';
import useTheme from './app/hooks/useTheme';
import { GestureHandlerRootView } from 'react-native-gesture-handler';
import Icon from 'react-native-vector-icons/MaterialCommunityIcons';
import ProjectsSection from './app/components/ProjectsSection';
import Breathe from './app/examples/breathe';
import { usePopup } from './app/navigation/popup/PopupProvider';
import Button from './app/components/buttons/Button';
import useDeviceSize, { DEVICE_SIZES } from './app/hooks/useDeviceSize';
import { concepts, personalProjects, professionalProjects } from './app/projects/data';

const experiences = [
  { name: 'react native', icon: 'react' },
  { name: 'typescript', icon: 'language-typescript' },
  { name: 'nodejs', icon: 'nodejs' },
  { name: 'serverless framework for aws with GraphQL', icon: 'aws' },
  { name: 'swift / Objective C & kotlin / java (basic knowledge)', icon: 'language-swift' },
  { name: 'Bitrise CI/CD', icon: 'hammer-wrench' },
  { name: 'react native skia', icon: 'draw' },
  { name: 'C# .NET', icon: 'language-csharp' },
];

const breatheTexts = [
  'Feeling tense?',
  'Need a break?',
  'Want some peace of mind?',
  'Stressed Out?',
  'Busy day?',
  'Tired?',
  'Take a moment for yourself.',
  'Breathe...',
];

const CONTAINER_WIDTH = 1400;

const App = () => {
  const theme = useTheme();
  const deviceSize = useDeviceSize();
  const { openPopup, closePopup } = usePopup();
  const [breatheText] = useState(breatheTexts[Math.floor(Math.random() * breatheTexts.length)]);

  return (
    <GestureHandlerRootView style={{ flex: 1 }}>
      <ScrollView
        style={{ flex: 1 }}
        contentContainerStyle={{
          padding: theme.space.m,
          marginTop: 100,
        }}
      >
        <View
          style={{
            alignSelf: 'center',
            maxWidth: CONTAINER_WIDTH,
            width: '100%',
          }}
        >
          {professionalProjects.length > 0 && (
            <ProjectsSection name="Professional work" projects={professionalProjects} />
          )}
          {personalProjects.length > 0 && (
            <ProjectsSection name="Personal work" projects={personalProjects} />
          )}
          {concepts.length > 0 && <ProjectsSection name="Concepts" projects={concepts} />}
          <View style={{}}>
            <Text
              style={{
                color: 'white',
                fontSize: 20,
                textTransform: 'uppercase',
                fontWeight: 'bold',
              }}
            >
              Notable experiences
            </Text>
            <ul style={{ marginBottom: 0 }}>
              {experiences.map((e) => (
                <li key={e.name} style={{ color: 'white' }}>
                  <Icon name={e.icon} size={20} color="white" style={{ marginRight: theme.space.s }} />
                  {e.name}
                </li>
              ))}
            </ul>
          </View>
          <Button
            text={breatheText}
            onPress={() => {
              openPopup({
                component: (
                  <View
                    style={{
                      marginVertical: theme.space.l,
                      marginHorizontal: theme.space.l,
                      alignItems: 'center',
                      gap: theme.space.m,
                    }}
                  >
                    <View
                      style={{
                        marginTop: -80,
                      }}
                    >
                      <Breathe />
                    </View>
                    <Text style={{ textAlign: 'center', marginTop: -100 }}>
                      Breathe with the cycle of the animation
                    </Text>
                    <Text style={{ textAlign: 'center' }}>
                      This is an open source react-native-skia example, powered by Skia
                    </Text>
                    <Button
                      inverted
                      text="Close"
                      onPress={() => {
                        closePopup();
                      }}
                      style={{
                        alignSelf: 'flex-end',
                      }}
                    />
                  </View>
                ),
              });
            }}
            style={{ marginTop: theme.space.xl, alignSelf: 'flex-end' }}
          />
        </View>
      </ScrollView>
      <View
        style={{
          flexDirection: 'row',
          justifyContent: 'space-between',
          position: 'fixed',
          backgroundColor: '#282c34',
          width: '100%',
          maxWidth: CONTAINER_WIDTH + theme.space.m * 2,
          alignSelf: 'center',
          height: 100 + 200, // some margin for overdrag
          padding: theme.space.m,
          paddingTop: theme.space.m + 200, // some margin for overdrag
          top: -200, // some margin for overdrag
        }}
      >
        <View>
          <Text
            style={{
              fontSize: 60,
              fontFamily: 'Louries',
              color: 'white',
            }}
          >
            Laurens
          </Text>
        </View>
        <View
          style={{
            flexDirection: deviceSize >= DEVICE_SIZES.s ? 'row' : undefined,
            gap: deviceSize >= DEVICE_SIZES.s ? theme.space.m : theme.space.s,
            alignSelf: 'center',
            alignItems: 'flex-end',
          }}
        >
          <Button text="Github" icon="github" link="https://github.com/laurens-lamberts" />
          <Button text="LinkedIn" icon="linkedin" link="https://nl.linkedin.com/in/laurenslamberts" />
        </View>
      </View>
    </GestureHandlerRootView>
  );
};

export default App;
